html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.6) 0%, #3b4862 100%), url(../img/header.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 3rem 0;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 15rem 0;
  }
}
.header__bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 10rem;
}
@media (min-width: 768px) {
  .header__content {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__text {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__text {
    display: block;
  }
}
.header__title {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header__title {
    text-align: left;
  }
}
.header__img {
  display: none;
}
@media (min-width: 768px) {
  .header__img {
    display: block;
    position: absolute;
    right: 0;
    bottom: -5rem;
    max-width: 35rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 5rem;
    opacity: 0.8;
  }
}
@media (min-width: 1200px) {
  .header__img {
    max-width: 60rem;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0rem;
  }
}
.navbar__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 2rem;
  transition: 0.3s;
}
.navbar__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navbar__link {
    margin-bottom: 0;
  }
}
.navbar__link:hover {
  opacity: 0.5;
}

.estates {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .estates {
    margin-top: 10rem;
  }
}
.estates__title {
  font-weight: 400;
}
.estates__title span {
  font-weight: 700;
}
.estates__title::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.estates__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .estates__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.estate {
  background-color: #FFFFFF;
}
.estate__img {
  aspect-ratio: 10/6;
  background-size: cover;
  position: relative;
}
.estate:nth-child(1) .estate__img {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/house1.jpg");
}
.estate:nth-child(2) .estate__img {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/house2.jpg");
}
.estate:nth-child(3) .estate__img {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/house3.jpg");
}
.estate:nth-child(4) .estate__img {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/house4.jpg");
}
.estate:nth-child(5) .estate__img {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/house5.jpg");
}
.estate:nth-child(6) .estate__img {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/house6.jpg");
}
.estate__name {
  position: absolute;
  left: 1rem;
  bottom: 0;
  color: #FFFFFF;
  font-size: 2.4rem;
}
.estate__content {
  padding: 2rem;
}
.estate__text {
  margin-bottom: 0;
}
.estate__price {
  font-size: 3rem;
  margin: 0;
}
.estate__link {
  background-color: #3b4862;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  /*    @include tablet {
         width: auto;
         display: inline-block;
     } */
  display: block;
  text-transform: uppercase;
}
.estate__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}

.featured {
  margin-top: 5rem;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/featured.jpg);
  background-size: 100%, cover;
  background-repeat: no-repeat;
  background-position: center, -100rem -40rem;
}
@media (min-width: 768px) {
  .featured {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url(../img/featured.jpg);
    background-position: center, -50rem center;
  }
}
.featured__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .featured__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.featured__content {
  padding: 5rem;
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .featured__content {
    grid-row: unset;
    padding: 10rem 5rem;
    grid-column: 2/3;
  }
}
.featured__title {
  color: #FFFFFF;
  text-transform: uppercase;
}
.featured__text {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.featured__price {
  color: #FFFFFF;
  font-size: 2.4rem;
  font-weight: 700;
}
.featured__link {
  background-color: #FFFFFF;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #3b4862;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  /*    @include tablet {
         width: auto;
         display: inline-block;
     } */
  display: block;
}
.featured__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}

.testimonial-blog {
  margin-top: 5rem;
}
@media (min-width: 992px) {
  .testimonial-blog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 992px) {
  .testimonial-blog__blog {
    grid-column: 2/4;
  }
}
.testimonial-blog__title {
  font-weight: 400;
}
.testimonial-blog__title span {
  font-weight: 700;
  display: block;
}
.testimonial-blog__title::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}

.testimonial {
  text-align: center;
}
.testimonial__name {
  font-size: 2.4rem;
  font-weight: 700;
}
.testimonial__img {
  width: 30rem;
  margin: 0 auto;
}
.testimonial__content {
  background-color: #FFFFFF;
  margin-top: -10rem;
  padding: 10rem 2rem 2rem 2rem;
}
.testimonial__text::before {
  content: "";
  background-image: url(../img/quote.svg);
  display: inline-block;
  width: 4rem;
  height: 4rem;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 1rem;
}

.entry {
  background-color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entry {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .entry__img-container {
    grid-column: 1/3;
  }
}
.entry__content {
  padding: 1rem;
}
@media (min-width: 768px) {
  .entry__content {
    grid-column: 3/6;
  }
}
.entry__img {
  height: 30rem;
  object-fit: cover;
  width: 100%;
}
.entry__meta {
  font-size: 1.4rem;
  color: rgb(103, 103, 195);
  text-transform: uppercase;
}
.entry__link {
  background-color: #3b4862;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
  /*    @include tablet {
         width: auto;
         display: inline-block;
     } */
}
.entry__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}

.footer {
  background-color: #3b4862;
  padding-top: 5rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__title {
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 2rem;
}
.footer__text {
  color: #FFFFFF;
}
.footer__navbar {
  display: flex;
  flex-direction: column;
}
.footer__link {
  font-size: 1.8rem;
  color: #FFFFFF;
  margin-bottom: 1rem;
  transition: 0.3s;
}
.footer__link:hover {
  opacity: 0.5;
}
.footer__link:last-of-type {
  margin-bottom: 0;
}
.footer__copyright {
  background-color: #283142;
  padding: 1rem;
  color: #FFFFFF;
  text-align: center;
  margin: 5rem 0 0 0;
}