@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    background-image: linear-gradient(to right, transparentize( v.$primary, .4 )0%,  v.$primary 100%,), url(../img/header.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 3rem 0;
    position: relative;
    @include m.tablet {
        padding: 3rem 0 15rem 0;

    }
    
    &__bar{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__logo{
        width: 10rem;
    }
    &__content{
        @include m.tablet {
            margin-top: 10rem;
            max-width: 50rem;
        }
    }
    &__text{
        color: v.$white;
        display: none;

        @include m.tablet {
            display: block;
        }
    }
    &__title{
        color: v.$white;
        text-align: center;
        @include m.tablet {
            text-align: left;
        }
       
    }
    &__img{
        display: none;
        
        @include m.tablet {
            display: block;
            position: absolute;
            right: 0;
            bottom: -5rem;
            max-width: 35rem;
            border-top-left-radius: 2rem;
            border-bottom-left-radius: 5rem;
            opacity: 0.8;
        }
        @include m.desktopxl {
            max-width: 60rem;
        }
    }
}

.navbar{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;

    @include m.tablet {
        flex-direction: row;
        gap: 2rem;
        margin-top: 0rem;
    }

    &__link{
        color: v.$white;
        font-size: 1.8rem;
        margin-bottom: 2rem;
        transition: 0.3s;
        &:last-of-type{
            margin-bottom: 0;
        }
        @include m.tablet {
            margin-bottom: 0;
        }

        &:hover{
            opacity: 0.5;
        }
    }
}