@use 'base/mixins' as m;
@use 'base/variables' as v;

.estates{
    margin-top: 5rem;

    @include m.tablet {
    margin-top: 10rem;
    }

    &__title{
        @include m.heading;
    }

    &__grid{
        @include m.grid(1, 2rem ) ;
        @include m.tablet {
            @include m.grid(3, 2rem ) ;
        }
    }
}

.estate{
    background-color: v.$white;

    &__img{
        aspect-ratio: 10/6;
        background-size: cover;
        position: relative;

    }

    @for $i from 1 through 6 {
        $image: '../img/house' + $i + '.jpg';

        &:nth-child(#{$i}) &__img{
            background-image: linear-gradient(to bottom, transparentize(v.$black, 1%) 0%, transparentize(v.$black, .4%) 100%), url($image);
        }
    }
    &__name{
        position: absolute;
        left: 1rem;
        bottom: 0;
        color: v.$white;
        font-size: 2.4rem;
    }
    &__content{
        padding: 2rem;
    }
    &__text{
        margin-bottom: 0;
    }
    &__price{
        font-size: 3rem;
        margin: 0;
    }
    &__link{
        @include m.btn(v.$primary, v.$white ) ;
        display: block;
        text-transform: uppercase;
    }
}