@use 'base/mixins' as m;
@use 'base/variables' as v;

.testimonial-blog{
    margin-top: 5rem;

    @include m.desktop {
        @include m.grid(3, 4rem ) ;
    }

    &__blog{
        @include m.desktop {
            grid-column: 2/4;
        }
    }
    &__title{
        @include m.heading(true);
    }
}

.testimonial{
    text-align: center;
    &__name{
        font-size: 2.4rem;
        font-weight: 700;
    }
    &__img{
        width: 30rem;
        margin: 0 auto;
    }
    &__content{
        background-color: v.$white;
        margin-top: -10rem;
        padding: 10rem 2rem 2rem 2rem;
    }
    &__text{
        &::before{
            content: '';
            background-image: url(../img/quote.svg);
            display: inline-block;
            width: 4rem;
            height: 4rem;
            background-repeat: no-repeat;
            background-position: center;
            margin-right: 1rem;
        }
    }
}

.entry{
    background-color: v.$white;
    margin-bottom: 2rem;
    @include m.tablet {
        @include m.grid(5, 2rem) ;
        align-items: center;
    }

    &__img-container{
        @include m.tablet {
            grid-column: 1/3;
        }
    }
    &__content{
        padding: 1rem;
        @include m.tablet {
            grid-column: 3/6;
        }
    }
    &__img{
        height: 30rem;
        object-fit: cover;
        width: 100%;
    }
    &__meta{
        font-size: 1.4rem;  
        color: rgb(103, 103, 195);
        text-transform: uppercase
    }
    &__link{
        @include m.btn(v.$primary, v.$white );
   
    }
}